import listItem from './list-item.vue'
export default {
	props: {
		tab: {
			type: Array,
			default() {
				return []
			}
		},
		tabIndex: {
			type: Number,
			default: 0
		}
	},
	components: {
		listItem
	},
	data() {
		return {};
	},
	methods: {
		change(e) {
			const { current } = e.detail
			this.$emit('changeTabIndex', current)
		}
	}
}
